.TodoItem {
    opacity: 0.5;
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 24px auto 0 auto;
    max-width: 1000px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 50px;
  }
  
  .TodoItem:last-child {
      margin-bottom: 40px;
  }

  .TodoItem:hover {
    opacity: 0.8;
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color:black;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    -webkit-text-stroke: 0.5px black;
    /* background-color: #CCC; */
  }
  
  .Icon-check {
    position: absolute;
    left: 12px;
  }

  .Icon-check:hover {
      color:#9df4a0
  }

  .Icon-check--active {
    color: #4caf50;
  }
  
  .Icon-delete {
    position: absolute;
    top: auto;
    right: 12px;
  }
  .Icon-delete:hover {
    color: red;
  }